
#inputbox {
  margin:0 auto;
  padding:10px 20px;
  max-width:500px;
  background:rgba(255, 255, 255, .4);
}
#inputbox .title {
  font-family: 'Pacifico', cursive;
  text-align:center;
  color:#444;
  margin:0 0 10px 0;
}
#inputbox input {
  font-family: 'Lato', sans-serif;
  font-weight:700;
  width:100%;
  padding:10px;
  box-sizing:border-box;
  border:none;
}



